import React, { Component } from 'react'
import { NavLink } from "react-router-dom";
import logo from "./kbc.png"

export default class Header extends Component {

    constructor(props) {
        super(props);
        this.prtformLogout = React.createRef();
    }

    handleLogoutClick = () => {
        localStorage.setItem("userLoginStatus", false);
    }

    render() {

        let participateButton, loginButton, logoutButton;

        if(this.props.participateBtn) {
            participateButton = <div className="col-auto">
                                    <NavLink className="btn btn-success" to="/survey" >Participate</NavLink>
                                </div>
        } else {
            participateButton = null;
        }

        if(this.props.loginBtn) {
            loginButton = <div className="col-auto">
                            <NavLink className="btn btn-success" to="/login">Login</NavLink>
                        </div>
        } else {
            loginButton = null;
        }

        if(this.props.logoutBtn) {
            logoutButton = <div className="col-auto">
                                <NavLink className="btn btn-danger" to="/login" ref={this.prtformLogout} >Logout</NavLink>
                            </div>
        } else {
            logoutButton = null;
        }

        return (
            <div className="row justify-content-center bg-primary">
                <div className="col-auto py-2">
                    <img src={logo} alt="logo" width="100px" />
                </div>
                <div className="col-auto py-3">
                    <h1 className="font-weight-bold title">Kon Banega karodpati</h1>
                </div>
                <div className="col-auto py-3 justify-content-end">
                    <div className="row mt-2">
                        {participateButton}
                        {loginButton}
                        {logoutButton}
                    </div>
                </div>
            </div>
        )
    }
}
